import React from 'react';
import { NavLink } from 'react-router-dom';
import "../App.css";
import logo from "../elements/Logo.png"

function Navbar() {
    return (
        <nav className='nav'>
            <img src={logo} className="logo"/>
            <NavLink to='/'><h2>Inicio</h2></NavLink>
            <NavLink to='/home'><h2>Home</h2></NavLink>
            <NavLink to='/login'><h2>Iniciar Sesion</h2></NavLink>
            <NavLink to='/register'><h2>Registro</h2></NavLink>
            <NavLink to='/home/history'><h2>Historial</h2></NavLink>
            <NavLink to='/home/stats'><h2>Estadística</h2></NavLink>
        </nav>
    );
}

export default Navbar;